@font-face {
  font-family: 'icomoon';
  src: url(/static/media/icomoon.9e7223a0.ttf) format('truetype'),
    url(/static/media/icomoon.336faa03.woff) format('woff'),
    url(/static/media/icomoon.bd126606.svg#icomoon) format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Oxygen', Roboto,
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: Montserrat, -apple-system, BlinkMacSystemFont, 'Segoe UI',
    'Oxygen', Roboto, 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
    'Helvetica Neue', sans-serif;
  font-weight: 500;
}

